/* fallback */
@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(https://fonts.gstatic.com/s/materialicons/v125/flUhRq6tzZclQEJ-Vdg-IuiaDsNc.woff2) format('woff2');
}

:root {
  --rc-ph: 1rem;
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  font-feature-settings: 'liga';
  -webkit-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
}

[data-theme="light"] {
  --col-main-highlight: #f0572d;
  --col-secondary-highlight: #31363f;
  --col-tertiary-highlight: #2b3a55;
  --col-main-contrast: #191b1d;
  --col-main-medium-contrast: #aeafb1;
  --col-main-low-contrast: #dfe4ea;
  --col-main-lower-contrast: #f9f9f9;
  --col-body: #ffffff;

  --col-shadow-medium: rgba(0, 0, 0, 0.30);
  --col-shadow-light: rgba(0, 0, 0, 0.15);
  --col-shadow-lightest: rgba(0, 0, 0, 0.075);

  --col-variant-red: #d4203e;
  --col-variant-red-lighter: #ffd0d0;
  --col-variant-blue: #3c54d8;
  --col-variant-blue-lighter: #6379f3;
  --col-variant-black: black;

  --col-loading-lighter: #ececec;
  --col-loading-darker: #e4e4e4;

  --col-confirm: #84b41a;
  --col-cancel: #d4203e;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
  scroll-behavior: smooth;
}

body {
  font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--col-body);
  color: var(--col-main-contrast);
  min-height: 100vh;
}

button {
  cursor: pointer;
}

#root {
  min-height: 100vh;
  display: grid;
  grid-template-rows: 1fr -webkit-max-content;
  grid-template-rows: 1fr max-content;
}

[data-menuopen="true"],
[data-modalopen="true"] {
  overflow: hidden;
}

@media (min-width: 499px) {
  :root {
    --rc-ph: clamp(2rem, 15vw, 10rem)
  }
}

@media (min-width: 2000px) {
  :root {
    --rc-ph: 15vw;
  }
}

@media (min-width: 3100px) {
  :root {
    --rc-ph: 25vw;
  }
}
